export default {
    data() {
        return {
            coloe: '',
            title: '',
            type: '',
            id: '',
            info: {}
        };
    },
    onLoad(options) {
        console.log(options);
        var title = '';
        if (options.type === '1') {
            title = '转让';
        } else if (options.type === '2') {
            title = '出售';
        } else {
            return false;
        }
        uni.setNavigationBarTitle({
            title: '服务器' + title
        });
        this.title = title;
        this.info = JSON.parse(options.info);
        this.type = options.type;
    },
    methods: {
        end: function(e) {
            this.coloe = e.detail.value;
        },
        btn: function() {
            if (!this.coloe || isNaN(parseInt(this.coloe))) {
                // 未填数量提示
                uni.showToast({
                    title: '请填写数量',
                    icon: 'none'
                });
                return false;
            } else if (this.coloe > this.info.lens) {
                // 未填数量提示
                uni.showToast({
                    title: '数量不够',
                    icon: 'none'
                });
                return false;
            }
            this.info.coloe = this.coloe;
            uni.navigateTo({
                url: '../server-sale/server-sale?type=' + this.type + '&info=' + JSON.stringify(this.info)
            });
        }
    }
};
